.btn-back {
	position: absolute;
	cursor: pointer;
	left: 50px;
	top: 100px;
	background-color: transparent;
	outline: none;
}

.icon-back {
	color: var(--color-black);
	font-size: 2.5rem;
	background-color: transparent;
	outline: none;
}

.icon-back:hover {
	transform: translateY(-3px);
	filter: drop-shadow(5px 5px 2px rgb(0 0 0 / 34%));
}

.p-detail-header-container {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	word-break: break-all;
	padding-bottom: 10px;
}

.header-text {
	font-size: 2em;
	text-transform: capitalize;
}

.header-id {
	font-size: 0.8em;
	color: var(--color-grey-drk);
	align-self: flex-end;
	display: inline;
}

.p-detail-container {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;

	max-width: 650px;
	margin: 0 auto;
}

.p-detail-img-container,
.p-detail-info-container {
	width: 50%;
	position: relative;
	align-self: center;
}

.p-detail-img {
	width: 100%;
	animation: jump 1s;
	animation-iteration-count: infinite;
	backface-visibility: hidden;
	filter: drop-shadow(20px 2px 6px rgb(0 0 0 / 35%));
}

.nickname-text {
	font-size: 1.8em;
	font-weight: bold;
	text-transform: capitalize;
	text-align: center;
	color: var(--color-blue);
}

.button-container {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.btn-catch-pokemon,
.btn-release-pokemon {
	padding: 10px;
}

.p-detail-info-box {
	display: flex;
	flex-direction: row;

	height: 80%;
	min-height: 250px;
	padding: 5%;

	border-radius: 20px;
	box-shadow: 0px 0px 10px 2px rgb(0 0 0 / 25%);
}

.p-detail-info-box-left,
.p-detail-info-box-right {
	width: 100%;
}

.p-detail-info-box-left {
	display: flex;
	flex-direction: column;
	max-width: 150px;
	width: 100%;
}

.box-text-title,
.box-text-value {
	display: block;
	font-size: 1.2em;
	font-weight: bold;
	padding: 5px;
	width: 100%;
	margin: 0;
}

.box-text-value {
	font-size: 1em;
	color: var(--color-white);
}

.box-ability {
	height: 100%;
}

.abilitiy {
	font-size: 1em;
}

.abilities {
	text-transform: capitalize;
}

.box-height,
.box-weight {
	height: 50%;
}

.p-detail-info-box-right {
	position: relative;
}

.p-detail-info-type-box {
	height: 20%;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	padding: 3%;
}

@media screen and (max-width: 640px) {
	.btn-back {
		left: 18px;
	}

	.icon-back {
		font-size: 2rem;
	}

	.p-detail-img-container,
	.p-detail-info-container {
		width: 100%;
	}

	.p-detail-info-container {
		padding-top: 0;
	}

	.catch-pokemon {
		position: relative;
		text-align: center;
		bottom: 0;
	}

	.p-detail-info-box-left {
		max-width: 100%;
	}
}

@keyframes jump {
	10%,
	35% {
		transform: translate3d(0, -3.5px, 0);
	}

	25% {
		transform: translate3d(0, -6px, 0);
	}

	0%,
	50%,
	100% {
		transform: translate3d(0, 0, 0);
	}

	60%,
	95% {
		transform: translate3d(0, 3.5px, 0);
	}

	85% {
		transform: translate3d(0, 6px, 0);
	}
}
