.icon-pokedex-container {
	position: absolute;
	left: 50%;
	top: 100px;
	cursor: pointer;
	transform: translate(-50%, 0);
}

.icon-pokedex-container:hover {
	transform: translate(-20px, -5px);
	filter: drop-shadow(2px 4px 4px var(--color-black));
}

.icon-pokedex {
	width: 50px;
	height: 100%;
}

.card-container {
	display: flex;
	position: relative;
	width: 100%;
	height: 100%;
	max-width: 320px;
	margin: 0 auto;
}

.icon-release-box {
	position: absolute;
	display: flex;
	right: 15px;
	top: 20px;
	z-index: 5;
	cursor: pointer;
	align-items: center;
	justify-content: center;
}

.icon-release {
	color: var(--color-black);
	font-size: 2.5em;
}

.icon-release-box:hover .icon-release {
	transform: translateY(-5px);
	color: var(--color-red-drk);
}

@media screen and (max-width: 640px) {
	.icon-release-box {
		right: 10px;
		top: 15px;
	}
}
