.btn {
	padding: 1rem;
	font-size: 1.3rem;
	font-weight: bold;
	outline: none;
	cursor: pointer;
	border: 3px solid var(--color-black);
	white-space: nowrap;
	background-color: var(--color-red);
	color: var(--color-white);
	border-radius: 30px;
	text-transform: uppercase;
	filter: drop-shadow(0px 2px 1px black);
}

.btn:hover {
	transition: none 0.1s ease-in;
	filter: brightness(200%);
	transform: translateY(-2px);
}

.size-modal,
.size-normal {
	font-size: 1rem;
	width: 115px;
}

.size-pagination {
	font-size: 1rem;
	width: 90px;
	padding: 0.8rem;
}

@media screen and (max-width: 640px) {
	.size-modal,
	.size-normal {
		font-size: 0.7rem;
		width: 90px;
	}
}
